nav.nav_top {
  height: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #464950;
  z-index: 5;
  box-shadow: none;
}
nav.nav_top i {
  color: #fff;
  font-size: 16px;
  line-height: 56px;
}

.logo_cont {
  width: 52px;
  height: 46px;
  position: relative;
}
.logo_cont img.logo {
  position: absolute;
  left: 0;
  height: auto;
  width: 100%;
  transition: all 0.3s;
}
.logo_cont .logo_bg {
  position: absolute;
  top: 60px;
  left: 0px;
  height: 20px;
  width: 52px;
  position: absolute;
  transition: opacity 0.7s;
  overflow: hidden;
}
.logo_cont .logo_bg .logo_bg_in {
  width: 100%;
  height: 100%;
  position: relative;
}
.logo_cont .logo_bg .bg_w {
  width: 80px;
  height: 80px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  bottom: 3px;
  box-shadow: 0px 3px 1px -1px rgba(0, 0, 0, 0.2);
}

nav.side {
  width: 0;
  background-color: #00254c;
  overflow: hidden;
  position: fixed;
  top: 46px;
  bottom: 0;
  height: 100%;
  left: 0;
  box-shadow: -2px 0px 3px 0px rgba(0, 0, 0, 0.5) inset;
  z-index: 3;
  transition: width 0.3s;
}
.no-menu nav.side {
  width: 0;
}
.only_icons nav.side {
  width: 60px;
}
.full_menu nav.side {
  width: 200px;
}
nav.side ul {
  padding: 0;
  list-style: none;
}
nav.side ul li {
  width: 200px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  position: relative;
  align-items: center;
  display: flex;
}
nav.side ul li a {
  display: flex;
  align-items: center;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0;
}
nav.side ul span.nav_icon {
  display: inline-block;
  width: 60px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-filter: brightness(1.8);
  filter: brightness(1.8);
}
nav.side ul span.nav_icon.no {
  width: 15px;
}
nav.side ul span.nav_icon img {
  height: 20px;
  max-width: 22px;
}
nav.side ul span.text {
  margin-left: 5px;
  color: #fff;
  line-height: 0;
}

nav.nav-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  z-index: 6;
  width: 0;
  transition-delay: 1s;
  transition-duration: 0s;
  transition-property: width;
  overflow: hidden;
}
nav.nav-modal .bgdark {
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.3s;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.menu_open nav.nav-modal {
  width: 100%;
  transition-delay: 0s;
  transition-duration: 0s;
  transition-property: width;
}
.menu_open nav.nav-modal .bgdark {
  background-color: rgba(0, 0, 0, 0.7);
}
nav.nav-modal .close_nav {
  height: 47px;
  border-bottom: 1px solid;
  display: flex;
  align-items: center;
}
nav.nav-modal .ul_par {
  display: flex;
  flex-direction: row;
}
nav.nav-modal .side_cont {
  width: 300px;
  background-color: #ffffff;
  height: 100%;
  transform: translatex(-300px);
  transition: 0.5s all 0.4s;
  box-shadow: 2px 0 4px 0 rgba(0, 0, 0, 0.3);
}
.menu_open nav.nav-modal .side_cont {
  transform: translatex(0px);
}
nav.nav-modal .side_cont ul {
  padding: 0;
  list-style: none;
}
nav.nav-modal .side_cont ul li {
  width: 300px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  position: relative;
  align-items: center;
  display: flex;
}
nav.nav-modal .side_cont ul li a {
  display: flex;
  align-items: center;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0;
}
nav.nav-modal .side_cont ul span.nav_icon {
  display: inline-block;
  width: 60px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-filter: brightness(1);
  filter: brightness(1);
}
nav.nav-modal .side_cont ul span.nav_icon.no {
  width: 15px;
}
nav.nav-modal .side_cont ul span.nav_icon img {
  height: 20px;
  max-width: 22px;
}
nav.nav-modal .side_cont ul span.text {
  margin-left: 5px;
  color: #000;
  line-height: 0;
}
nav.nav-modal .side_cont ul {
  width: 0;
  transition: width 0.3s;
  z-index: 1;
}
nav.nav-modal .side_cont ul li.first_sub {
  background-color: #004280;
}
nav.nav-modal .side_cont ul li.first_sub i.fas {
  color: #fff;
}
nav.nav-modal .side_cont ul li.first_sub span.text {
  color: #fff;
}
nav.nav-modal .side_cont ul li.first_sub .nav_icon img {
  filter: saturate(11);
}
nav.nav-modal .side_cont ul.sub_cat {
  z-index: 2;
  overflow: hidden;
  width: 0;
  background-color: #fff;
}
nav.nav-modal .side_cont ul.sub_cat.active {
  width: 300px;
}
nav.nav-modal img.top_img {
  width: 100%;
}

.icon-par {
  display: flex;
  position: absolute;
  right: 20px;
}
.icon-par.ip1 {
  left: 20px;
  right: auto;
}

.nav-icon {
  cursor: pointer;
  width: 44px;
  height: 44px;
  margin-left: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.nav-icon i {
  position: relative;
  z-index: 1;
  font-size: 16px;
  line-height: 56px;
  color: #616060;
}

.bg-effect {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition: all 0.3s;
}
.nav-icon:hover .bg-effect {
  transform: scale(1);
}

/*---------------------------------------------<popup>------------------*/
.popover_html_cont {
  display: none;
}

.popover-body {
  padding: 12px 20px;
}
.popover-body .dropdown-menu {
  position: relative;
  box-shadow: none;
  float: none;
  min-width: 10rem;
  padding: 0;
  margin: 0;
  border: 0;
}

.user {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem 1.5rem;
  margin-bottom: 0.5rem;
  border-bottom: 1px solid #dedede;
}
.user .h3 {
  color: #e2e2e2;
}
.user p {
  margin-bottom: 0;
}

.avatar {
  margin-bottom: 15px;
}
.avatar img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  object-position: center center;
  border-radius: 50%;
}

/*---------------------------------------------</popup>------------------*/
/*-------------------------------<nav-head>----------------------------------*/
.nav-head {
  background: #2e435e;
  background: linear-gradient(144deg, #2e435e 49%, #1eb6db 100%);
  position: relative;
}
.nav-head .logo-side {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 20px 10px 20px;
  color: #ffffff;
  font-size: 50px;
}
.nav-head .logo-side i {
  font-size: 50px;
}
.nav-head .logo-side-img {
  width: 60px;
}
.nav-head .user-text {
  padding: 0px 20px;
  padding-bottom: 10px;
  text-align: center;
}
.nav-head .user-text p {
  color: #fff;
  margin: 0;
  line-height: 1.5;
}
.nav-head a {
  color: rgba(255, 255, 255, 0.7);
}

.nav-icon.popover-cont.close_icon {
  position: absolute;
  right: 20px;
  top: 7px;
  font-size: 27px;
  color: rgba(255, 255, 255, 0.8);
}
.nav-icon.popover-cont.close_icon img {
  width: 15px;
}

